.ServiceTag {
  background: #fef8e0;
  position: absolute;
  width: 100px;
  height: 30px;
  color: #fc6c00;
  border-radius: 50px;
}

.ServiceTag p {
  position: absolute;
  left: 62%;
  top: 0.5px;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 110px;
  font-weight: 800;
  font-size: 15px;
  letter-spacing: -1px;

}

.ServiceTag img {
  position: absolute;
  width: 42px;
  left: -8px;
  top: 2px;
}