.ChipButton {
  padding: 1px 6px 9px 10px;
  font-size: 12px;
  border-radius: 16px;
  color: #0c0c0c;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  box-shadow: 0 0 0 1px hsl(0, 0%, 60%) inset;
  margin-right: 8px;
  border: none;
  transition: 0.2s all ease;
}

.ChipButton:hover {
  box-shadow: 0 0 0 1px #FC6C00 inset;
}

.ChipButton_nonBorder {
  padding: 1px 5px 5px 10px;
  font-size: 12px;
  border-radius: 16px;
  color: #111820;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
}

.ChipButton_disable {
  padding: 1px 5px 5px 10px;
  font-size: 12px;
  border-radius: 16px;
  color: #111820;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  opacity: 0.8;
}

.ChipButton p {
  position: relative;
  display: inline;
  top: 1px;
  left: -5px;
  font-size: 13px;
}

.ChipButton img {
  position: relative;
  display: inline;
  width: 18px;
  left: 1px;
  top: 5.5px;
  margin-right: 10px;
}