.InputTag {
  position: relative;
  display: inline-block;
  width: 100%;
  background: white;
  border-radius: 8px;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif !important;
  margin-bottom: 10px;
}
.IT_title {
  position: relative;
  margin-bottom: 8px;
  font-size: 14px;
  left: 5px;
  font-weight: 400;
  /* font-stretch: condensed; */
}

.IT_input_normal {
  position: relative;
  width: calc(100% - 32px);
  height: 40px;
  box-shadow: 0 0 0 1px rgb(54, 54, 54);
  padding: 7px 16px;
  border-radius: 6px;
  background: white;
}

.IT_input_normal input {
  position: relative;
  width: calc(100% - 10px);
  top: 9px;
  font-size: 16px;
  font-weight: 600;
  height: 20px;
  border: none;
}

.IT_input_textarea {
  position: relative;
  width: calc(100% - 32px);
  box-shadow: 0 0 0 1px rgb(54, 54, 54);
  padding: 16px 16px;
  height: 200px;
  border-radius: 6px;
  word-break: break-all;
  background: white;
}

.IT_input_textarea p {
  position: absolute;
  right: 10px;
  bottom: 0px;
  font-size: 12px;

}

.IT_input_textarea textarea{
  position: relative;
  width: calc(100% - 0px);
  left: -2px;
  height: 195px;
  border-radius: 6px;
  background: white;
  border: none;
  resize: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: -0.5px;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif !important;
}

.IT_input_normal p {
  position: absolute;
  top: -4px;
  font-size: 14px;
}