.SmallButton {
  font-size: 14px;
  border-radius: 16px;
  color: #464646;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  border: none;
}

.SmallButton_icon {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  top: 3.5px;
  margin-right: 6px;
}

.SmallButton_icon img {
  position: relative;
  width: 100%;
  height: 100%;
}

.SmallButton_text {
  position: relative;
  display: inline-block;
}

.SmallButton_partition {
  position: relative;
  display: inline-block;
  margin-left: 10px;
  margin-right: 6px;
  font-size: 22px;
  top: 2px;
  color: #c8c8c8;
  font-weight: 200;
}