.TopBar {
  position: relative;
  width: calc(100% - 0px);
  height: 60px;
  background: #fff;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  margin-bottom: 15px;
}

.TopBar_locate {
  position: relative;
  width: calc(100% - 0px);
  height: 60px;
  background: #f6f7f9;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
}

.TopBar_locate img {
  width: 25px;
  position: relative;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  display: inline;
}

.TB_title {
  position: absolute;
  width: 80%;
  left: 0px;
  /* font-size: 18px; */
  top: 50%;
  transform: translateY(-50%);
  text-align: left;
  font-weight: 600;
  display: inline;
}

.TB_title_locate {
  position: relative;
  width: 80%;
  left: 10px;
  top: 10px;
  text-align: left;
  font-weight: 700;
  display: inline;
  font-size: 18px;
}

.TB_prev {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translate(0%, -50%);
  font-weight: 400;
  font-size: 12px;
  text-decoration: underline;
  color: #d1d1d1;
}