.ImportFile {
  position: relative;
  display: inline-block;
}

.IF_item_true {
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 8px;
  text-align: center;
  display: inline-block;
  background: #F6F7F9;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  margin-right: 5px;
  border: solid 1px #ffffff;
  overflow: hidden;
}

.IF_item_false {
  width: 80px;
  height: 80px;
  border-radius: 8px;
  text-align: center;
  display: inline-block;
  background: #F6F7F9;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  margin-right: 5px;
  overflow: auto;
  border: dashed 1px #6d6d6d;
  overflow: hidden;
}

.item_close {
  position: absolute;
  z-index: 1;
  right: 7px;
  top: 7px;
  width: 16px;
  height: 16px;
  overflow: hidden;
}

.item_img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  object-fit: cover;
}