.NormalButton {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 20px;
  width: calc(100% - 30px);
  padding: 15px;
  font-weight: 400;
  font-size: 16px;
  border-radius: 8px;
  text-align: center;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
}

.NB_primary {
  color: white;
  background: #FC6C00;
  cursor: pointer;
}

.NB_secondary {
  color: #FC6C00;
  box-shadow: 0 0 0 1px #FC6C00 inset;
  cursor: pointer;
}

.NB_disable {
  color: white;
  background: #D8E0EB;
}