.view {
    position: absolute;
    left: 0px;
    top: 0px;
    width: calc(100% - 20px);
    height: 100%;
    padding: 0px 10px 10px 10px;
}

.button_footer {
    position:absolute;
    width: calc(100% - 20px);
    height: 70px;
    bottom: 10px;
}

.section {
    position: relative;
    width: 100%;
    height: calc(100% - 70px);
}

.section_2 {
    position: relative;
    width: 100%;
    height: calc(100% - 70px);
}

.locatePage_top {
    background: #f6f7f9;
    width: calc(100% - 20px);
    padding-left: 10px;
    padding-right: 10px;
    height: 195px;
    border-radius: 0px 0px 16px 16px;
}

.top_map {
    position: relative;
    top: 5px;
    width: calc(100% - 150px);
    white-space: nowrap;
    overflow: scroll;
}

.top_smallicon_position {
    position: absolute;
    width: 160px;
    left: calc(100% - 155px);
    top: 175px;
}

.search_top {
    position: relative;
    height: 48px;
    width: 100%;
    border-bottom: 1px solid #e6e6e6;
}

.top_recent_position {
    position: absolute;
    top: 14px;
    left: 11px;
}

.top_edit_position {
    position: absolute;
    left: calc(100% - 65px);
    top: 14px;
    width: 120px;
}