.SearchBox {
  position: relative;
  width: calc(100% - 30px);
  border-radius: 16px;
  /* text-align: center; */
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  margin-bottom: 8px;
}

.SearchBox img {
  position: absolute;
  width: 24px;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.SB_gps_icon {
  position: absolute;
  right: 35px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
}

.SB_gps_icon img {
  width: 100%;
  position: absolute;
}

.SB_idle {
  padding: 20px 15px;
  font-size: 14px;
  color: #616161;
  background: #ffffff;
  text-align: left;
  transition: 0.2s all ease;
  /* box-shadow: 0 0 0 1px #d6d6d6 inset; */
}

.SB_idle p{
  position: relative;
  display: inline;
  left: 35px;
}

.SB_idle_locate {
  font-weight: 800;
  display: inline;
  position: absolute;
  left: 105px;
  font-size: 16px;
  width: calc(100% - 145px);
  height: 40px;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.SB_idle:hover {
  color: #FC6C00;
  background: #ffffff;
  text-align: left;
  box-shadow: 0 0 0 1px #FC6C00 inset;
}

.SB_active {
  /* padding: 20px 15px; */
  font-size: 14px;
  height: 58px;
  width: 100%;
  color: #FC6C00;
  box-shadow: 0 0 0 1px #FC6C00 inset;
  background: #fff;
}

.SB_active p{
  position: absolute;
  /* display: inline; */
  left: 48px;
  top: 4px;
  width: calc(100% - 70px);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 16px;
  font-weight: 800;
  color: #FC6C00;
}

.NB_disable {
  color: white;
  background: #D8E0EB;
}