.ListRow {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 74px;
  background: white;
  border-bottom: 1px #e6e6e6 solid;
}

.decoIcon {
  position: absolute;
  top: calc(50%);
  transform: translateY(-50%);
  left: 16px;
  width: 24px;
  height: 24px;
}

.decoIcon img {
  position: absolute;
  width: 100%;
}

.bigText {
  position: absolute;
  top: calc(50% - 9px);
  left: 48px;
  width: calc(100% - 120px);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  transform: translateY(-50%);
  font-size: 16px;
}

.textInfo {
  position: absolute;
  top: calc(50% + 11px);
  width: calc(100% - 120px);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  transform: translateY(-50%);
  left: 48px;
  font-size: 12px;
  color: #616161;
}

.selectButton {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  padding: 5px 14px;
  border: 1px solid #9e9e9e;
  border-radius: 8px;
  font-size: 14px;
  color: #9e9e9e;
  background: white;
}