@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);

.HistoryCard {
  position: relative;
  border-radius: 8px;
  color: rgb(62, 62, 62);
  width: calc(100% - 16px);
  font-weight: 600;
  background-color: #ffffff;
  padding: 16px 0px 36px 16px;
  display: inline-block;
  font-family: 나눔스퀘어, 'NanumSquare', sans-serif;
  box-shadow: 0 0 0 1px #d6d6d6 inset;
}

.hc_type {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 100px;
  height: 30px;
  border-radius: 50px;
}

.hc_type p {
  position: absolute;
  text-align: center;
  width: 70px;
  top: 1px;
  left: 60%;
  transform: translate(-50%, -50%);

}

.hc_date {
  position: relative;
  font-size: 18px;
  font-weight: 700;
}

.hc_partition {
  width: 24px;
  height: 40px;
  position: relative;
  top: 5px;
  display: inline-block;
  text-align: center;
  font-size: 16px;
  font-weight: 200;
}

.hc_name {
  position: relative;
  display: inline;
  font-weight: 400;
  top: 4px;
  font-size: 12px;
}

.hc_carnum {
  position: relative;
  display: inline;
  font-weight: 400;
  top: 4px;
  font-size: 12px;
}

.hc_dots_start {
  position: relative;
  display: inline-block;
  top: 4px;
  width: 20px;
  height: 20px;
  left: 0px;
}

.hc_dots_start img{
  position: absolute;
  width: 100%;
  height: 100%;
}

.hc_dots_end {
  position: relative;
  display: inline-block;
  top: 27px;
  width: 20px;
  height: 20px;
  left: 7px;
}

.hc_dots_end img{
  position: absolute;
  width: 30%;
  height: 30%;
}

.hc_startTime {
  position: relative;
  display: inline;
  font-weight: 400;
  top: -1px;
  left: 5px;
  font-size: 16px;
}

.hc_startPoint {
  position: relative;
  display: inline;
  font-weight: 400;
  top: -1px;
  left: 22px;
  font-size: 16px;
}

.hc_endTime {
  position: relative;
  display: inline;
  font-weight: 400;
  top: 16px;
  left: 5px;
  font-size: 16px;
}

.hc_endPoint {
  position: relative;
  display: inline;
  font-weight: 400;
  top: 16px;
  left: 22px;
  font-size: 16px;
}
